import StartPresenter from './StartPresenter';
import { useNavigate } from 'react-router';
import { useEffect, useState } from 'react';
// @ts-ignore
import { getCookie, setCookie, removeCookie } from 'Cookie.ts';
import axios from 'axios';
import React from 'react';

function StartConatiner () {
  const navigate = useNavigate();
  const [user,setUser] = useState(null);

  useEffect(()=>{
    if(auth()){
      axios.post('http://13.125.81.51:3003/apis/auth/authToken', {
        token:getCookie('USER')
    },{withCredentials:true
    })
    .then((response)=>{
      setUser(response.data);
      });
    }
  },[]);

  function goMain(){
    if(getCookie('USER')){
      window.location.assign(`/main`);
    }//    
  }

  function goSignup(){
    navigate('/signup');
  }

  function goSignin(){
    navigate('/signin');
  }

  function auth(){
    if(getCookie('USER')){
      return true;
    }else{
      return false;
    };
  }

    return (
        <StartPresenter
        goMain={goMain}
        goSignup={goSignup}
        goSignin={goSignin}
        auth = {auth}
        user = {user}/>
    )
}

export default StartConatiner;